.individual-interest {
  position: relative;
  display: flex;
  flex-direction: column;
  margin: 10px;
  padding: 20px;
  align-items: center;
}
.interest-name {
  position: relative;
  display: flex;
  width: fit-content;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 25px;
  margin: 20px;
  text-align: center;
}
.interest-description {
  position: relative;
  display: flex;
  font-size: larger;
  flex: 1;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 500;
  text-align: center;
  color: darkslategray;
}

@media screen and (max-width: 460px) {
  .interest-name {
    font-size: 20px;
  }
  .interest-description {
    font-size: 15px;
  }
}
